import { lazy } from 'react';
import { Route, Routes, Navigate } from 'react-router-dom';

import Layout from 'components/Layout';

import { GlobalStyle } from 'components/GlobalStyle';

import moviesRefs from 'data/moviesRefs.json';

const HomePage = lazy(() => import('./pages/HomePage'));
const SearchPage = lazy(() => import('./pages/SearchPage'));
const MovieDetailsPage = lazy(() => import('./pages/MovieDetailsPage'));
const MovieCast = lazy(() => import('./components/MovieCast'));
const MovieReviews = lazy(() => import('./components/MovieReviews'));

const App = () => {
  return (
    <>
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<Navigate to="tranding-day" />} />

          {moviesRefs.map(({ name, title, ref }) => (
            <Route
              key={name}
              path={name}
              element={<HomePage title={title} movieRef={ref} />}
            />
          ))}

          <Route path="movies" element={<SearchPage />} />
          <Route path="movies/:movieId" element={<MovieDetailsPage />}>
            <Route path="cast" element={<MovieCast />} />
            <Route path="reviews" element={<MovieReviews />} />
          </Route>

          <Route path="*" element={<div>Not Found</div>} />
        </Route>
      </Routes>
      <GlobalStyle />
    </>
  );
};

export default App;

// '/'   Home,      .
// '/movies'   Movies,      .
// '/movies/:movieId'   MovieDetails,      .
// /movies/:movieId/cast   Cast,    .    MovieDetails.
// /movies/:movieId/reviews   Reviews,   .    MovieDetails.
